<template>
  <div>
    <div class="google-map fill-height" ref="googleMap" />
    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t('generic.Detailes') +' ' + routeName "
      :okText=" $t('modal.AddNew') "
      :cancelText=" $t('modal.cancel')">
      <iframe src="https://www.google.com/maps/embed?pb=!1m24!1m12!1m3!1d115932.83613392277!2d46.78744985151215!3d24.764578866475812!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!4m9!3e0!4m3!3m2!1d24.6919799!2d46.6717065!4m3!3m2!1d24.8432674!2d46.7353106!5e0!3m2!1sar!2ssa!4v1673394582208!5m2!1sar!2ssa" width="600" height="450" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
    </va-modal>
  </div>
</template>

<script>
import * as GoogleMapsLoader from 'google-maps'

export default {
  name: 'google-map',
  data () {
    return {
      routeName: '',
      createModal: false,
    }
  },
  mounted () {
    const self = this
    GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
    /* We stick to version updates instead of weekly channel update because
       using weekly updates you need to test them, but we prefer to use stable
       versions. */
    GoogleMapsLoader.VERSION = '3.35'
    GoogleMapsLoader.load((google) => {
      /* eslint-disable no-new */
      const map = new google.maps.Map(this.$refs.googleMap, {
        center: new google.maps.LatLng(24.774265, 46.738586),
        zoom: 11,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      })

      // const trafficLayer = new google.maps.TrafficLayer()

      // trafficLayer.setMap(map)

      var routes = [
        {
          origin: new google.maps.LatLng(24.696446214860174, 46.703075887586145),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.714523273595294, 46.823145416155626),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.817984171382186, 46.59366072235679),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.91405527182876, 46.80472804931016),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.76518785411942, 46.80550539629352),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.687550918593196, 46.81777779788274),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.64447056771104, 46.73802076197436),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.635849084130076, 46.664953236248834),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        }]
      var stepDisplay = new google.maps.InfoWindow()

      var directionsService = new google.maps.DirectionsService()
      function getPoly () {
        var letters = '0123456789ABCDEF'
        var color = '#'
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)]
        }
        var polylineOptionsActual = new google.maps.Polyline({
          strokeColor: color,
          geodesic: true,
          strokeOpacity: 0.8,
          strokeWeight: 6,
        })
        return polylineOptionsActual
      }
      routes.forEach((route, i) => {
        var po = getPoly()
        var rendererOptions = {
          preserveViewport: true,
          suppressMarkers: true,
          routeIndex: Math.random(),
          polylineOptions: po,
        }
        var request = {
          origin: route.origin,
          destination: route.destination,
          travelMode: google.maps.TravelMode.DRIVING,
        }

        var directionsDisplay = new google.maps.DirectionsRenderer(rendererOptions)
        directionsDisplay.setMap(map)
        google.maps.event.addListener(po, 'click', function (evt) {
          console.log('route' + i)
          self.routeName = 'السليمانية  ' + i
          self.createModal = true
          stepDisplay.setContent('you clicked on the route<br>' + evt.latLng.toUrlValue(6))
          stepDisplay.setPosition(evt.latLng)
          stepDisplay.open(map)
        })
        directionsService.route(request, function (result, status) {
          console.log(result)
          new google.maps.Marker({
            position: new google.maps.LatLng(route.origin),
            map: map,
          })
          if (status == google.maps.DirectionsStatus.OK) {
            directionsDisplay.setDirections(result)
          }
        })
      })

      new google.maps.Marker({
        position: new google.maps.LatLng(routes[0].destination),
        map: map,
        animation: google.maps.Animation.BOUNCE,
        icon: 'https://gcdnb.pbrd.co/images/hnKUay2TdxIv.png',
      })
    })
  },
}
</script>
